@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
* {
  margin: 0px;
  padding: 0px;
}

body {
  margin: 0;
  height: 100%;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  height: 100%;
  margin: 0px;
  background-color: #F3F2ef;
}

.home {
    margin-bottom: 50px;
    min-height: 100%;
}
.navbar {
    display: flex;
    padding: 12px 24px;
    justify-content: space-between;
    background-color: white;
    position: relative;
}

.titleBox {
    display: flex;
    justify-content: center;
    align-items: center;
}

.titleBox > * {
    display: block;
    margin: 0px 10px;
}

.title {
    font-size: 28px;
    font-weight: 500;
}

.verticalCenter {
    display: flex;
    justify-content: center;
    align-items: center;
}

.profilePhoto {
    height: 35px;
    border-style: solid;
    border-width: 3px;
    border-color: #BC4749;
    border-radius: 50px;
}
.signInBtn {
    background-color: #BC4749;
    max-width: 150px;
    text-align: center;
    padding: 8px 16px;
    border-radius: 4px;
    color:white;
    font-size: 14px;
    font-weight: 500;

    display: flex;
    justify-content: center;
    align-items: center;
}
.comment {
    padding: 3px 12px;
  }

.commentInput {
    display: flex;
    justify-content: space-between;
    padding: 16px 12px;
  }
  
  .commentInput__text {
    flex: 1 1;
  }
  .commentInput__button {
    background-color: white;
    border: none;
    margin-left: 24px;
    color: black;
    font-weight: 500;
  }
  
  .commentInput__textarea {
    border: none;
    width: 100%;
    font-size: 14px;
    resize: none;
    margin: 8px 0px;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  }
  
  .commentInput__textarea {
    border: none;
  }
  
  .commentInput__textarea:focus {
    outline: none;
  }
.createPost {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 24px;
}
.createPost__imageUpload > input{
    display: none;
}

.createPost__loggedIn{
    background-color: white;
    width: 100%;
    max-width: 600px;
    border-radius: 4px;
    padding: 12px 16px;
}

.createPost__uploadButton{
    border: none;
    background-color: white;
}
.createPost_loggedInBottom{
    display: flex;
    justify-content: space-between;
}
.createPost__textArea{
    border: none;
    width: 100%;
    outline: none;
    font-size: 14px;
    margin-top: 6px;
    font-family: "Poppins", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.createPost__textArea :focus{
    outline: none;
}
#image-preview{
    display: none;
    max-height: 80px;
    margin: 12px 0px;
    border-radius: 4px;
}
.post{
    background-color: white;
    max-width: 600px;
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
;
    right: 500px;
}
.post__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    object-fit: contain;
    padding-top: 10px;
}
.post__headerLeft{
    display: flex;
    align-items: center;
}
 
.post__headerprofilePic{
    height: 35px;
    border-radius: 8px;
}
.post__delete{
    border: none;
    background-color: white;
    cursor: pointer;
}

.post__delete:focus{
outline: none;

}

.post__photoUrl{
    width: 100%;
    margin: 16px 0px;
    object-fit: cover;
}

.post__caption {
    margin: 0px 0px 50px 15px;
    padding-bottom: 15px;
}
